
import Vue from 'vue'

export default {
	globalData:{
			windowHeight:0,
			windowWidth:0,
			isNewUser:false,
			 
			userInfo:"",
			hasUserInfo: false,
			
			sessionCode:"",
			
			openid:"", // 用户唯一标志
			appid:""  ,// 所用的小程序id

			unionid:"",
			stuRegister:false  /* 在进入页面的时候获取该信息 */
		},
	onLaunch: function() {
		// 展示本地存储能力
		        var logs = wx.getStorageSync('logs') || []
		        logs.unshift(Date.now())
		        wx.setStorageSync('logs', logs)

		// 登录 获取sessioncode
		// var _this = this;
		wx.login({
			success: (res) => {
				// 发送 res.code 到后台换取 openId, sessionKey, unionId
				console.log("登录时获取的信息 主要是sessionCode", res, this)
				this.globalData.sessionCode = res.code
			}
		})
		
		// 生命周期函数 当页面加载时进行调用
		// 初始化云开发环境
		wx.cloud.init({
		env:"wlq-1011"
		}) 
		
		const db = wx.cloud.database().collection("userInfo")
		const stuRegistDatabase = wx.cloud.database().collection("studentRegister")
		// 检查用户信息表中是否有该用户 isNewUser 以便于后期获取用户信息 
		// 调用云函数获取用户 openid 
		wx.cloud.callFunction({
			name:"getopenid",
			success(res){
				console.log("获取用户openid信息成功。。",res)
				getApp().globalData.openid = res.result.openid,
				getApp().globalData.appid = res.result.appid
			} 
		})
		
		/* 获取用户信息 */
		wx.getSetting({
		    success: res => {
		        if (res.authSetting['scope.userInfo']) {
		          // 已经授权，可以直接调用 getUserInfo 获取头像昵称，不会弹框
					wx.getUserInfo({
						success: res => {
						// 可以将 res 发送给后台解码出 unionId
						this.globalData.userInfo = res.userInfo
						this.globalData.hasUserInfo = true
						console.log("APP.vue中的获取用户信息", this.globalData.userInfo)
						if (this.userInfoReadyCallback) {
							this.userInfoReadyCallback(res)
							}
						}
					})
		        }
		    }
		})
		
		/* 检查是否进行学生注册 */
		setTimeout(function(){
			stuRegistDatabase.where({
				_openid: getApp().globalData.openid
			}).get({
				success(res){
					console.log(res.data.length)
					if (res.data.length == 0){
						getApp().globalData.stuRegister = false
						console.log("是一个新学生用户需要进行注册，目前状态",getApp().globalData.stuRegister)
					}
					else{
						console.log("数据库中有该学生的用户信息",res)
						getApp().globalData.stuRegister = true
					}	
				}
			}) 
		},3000) 
		
		// setTimeout(function(){
		// 	db.where({
		// 		_openid: getApp().globalData.openid
		// 	}).get({
		// 		success(res){
		// 			console.log(res.data.length)
		// 			if (res.data.length == 0){
		// 				getApp().globalData.isNewUser = true
		// 				/* 公共的基础注册信息 */
		// 				console.log("是一个新用户需要进行注册，目前状态",getApp().globalData.isNewUser)
		// 			}
		// 			else{
		// 				console.log("数据库中有该用户信息",res)
		// 				getApp().globalData.isNewUser = false
		// 			}	
		// 		}
		// 	})
		// },3000) 
		
		try {
			const res = uni.getSystemInfoSync();
			this.globalData.windowHeight = res.windowHeight;
			this.globalData.windowWidth = res.windowWidth;
			console.log(res)
			console.log("onlaunch时得到的手机尺寸数据",this.globalData.windowHeight, this.globalData.windowWidth)
		} catch (e) {  
			console.log("无法获取手机信息")
		}
			 
		uni.getSystemInfo({
			success: function(e) {










				Vue.prototype.StatusBar = e.statusBarHeight;
				let custom = wx.getMenuButtonBoundingClientRect();
				Vue.prototype.Custom = custom;
				Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;






			}
		}) 

	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}

}
